html,
body,
.app,
.main {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  padding-bottom: 60px; }

.slider {
  position: relative;
  width: 100%;
  margin: 0 auto;
  height: 100%;
  overflow: hidden;
  white-space: nowrap; }
  .slider .slider-wrapper {
    position: relative;
    height: 100%;
    width: 100%; }
    .slider .slider-wrapper .slide {
      display: inline-block;
      height: 100%;
      width: 100%; }

.slider-right-arrow,
.slider-left-arrow {
  height: 50px;
  width: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: #f9f9f9;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: -webkit-transform ease-in 0.1s;
  transition: -webkit-transform ease-in 0.1s;
  -o-transition: transform ease-in 0.1s;
  transition: transform ease-in 0.1s;
  transition: transform ease-in 0.1s, -webkit-transform ease-in 0.1s; }
  .slider-right-arrow:hover,
  .slider-left-arrow:hover {
    -webkit-transition: -webkit-transform ease-in 0.1s;
    transition: -webkit-transform ease-in 0.1s;
    -o-transition: transform ease-in 0.1s;
    transition: transform ease-in 0.1s;
    transition: transform ease-in 0.1s, -webkit-transform ease-in 0.1s;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1); }

.slider-right-arrow {
  position: absolute;
  top: 50%;
  right: 25px;
  z-index: 999;
  color: #fff; }
  .slider-right-arrow img {
    -webkit-transform: translate(2px, 0);
        -ms-transform: translate(2px, 0);
            transform: translate(2px, 0); }
    .slider-right-arrow img:focus {
      outline: 0; }

.slider-left-arrow {
  position: absolute;
  top: 50%;
  left: 25px;
  z-index: 999;
  color: #fff; }
  .slider-left-arrow img {
    -webkit-transform: translate(-2px, 0);
        -ms-transform: translate(-2px, 0);
            transform: translate(-2px, 0); }
    .slider-left-arrow img:focus {
      outline: 0; }

.dots-container {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  z-index: 1000;
  padding: 5px 0; }
  .dots-container .dot {
    background: #222;
    display: inline-block;
    padding: 10px;
    margin-right: 5px;
    cursor: pointer;
    border-radius: 50%; }
  .dots-container .active {
    background: #fff; }

.autoplay {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px 10px;
  background: black;
  color: #c7c7c7;
  font-size: 15px; }
  .autoplay p {
    margin: 0; }



